










































import Vue from 'vue';
import { Digimon } from '@/data/models/Digimon';
import DigimonFilter from './Filter.vue';
import { Filter } from '../data/models/Filter';

export default Vue.extend({
  components: {
    'digimon-filter': DigimonFilter,
  },
  data: () => ({
    search: '',
    headers: [
      {
        text: 'Name',
        align: 'start',
        sortable: true,
        value: 'name',
      },
      {
        text: 'Rank',
        align: 'center',
        sortable: true,
        value: 'rank',
      },
      {
        text: 'Form',
        align: 'center',
        sortable: true,
        value: 'form',
      },
      {
        text: 'Basic Attr.',
        align: 'center',
        sortable: true,
        value: 'basicAttribute',
      },
      {
        text: 'Elemental Attr.',
        align: 'center',
        sortable: true,
        value: 'attribute',
      },
      {
        text: 'DPS (Damage Per Second)',
        align: 'center',
        sortable: true,
        value: 'dps',
      },
      {
        text: 'HP',
        align: 'center',
        sortable: true,
        value: 'hp',
      },
      {
        text: 'DS',
        align: 'center',
        sortable: true,
        value: 'ds',
      },
      {
        text: 'AT',
        align: 'center',
        sortable: true,
        value: 'at',
      },
      {
        text: 'AS',
        align: 'center',
        sortable: true,
        value: 'as',
      },
      {
        text: 'CT',
        align: 'center',
        sortable: true,
        value: 'ct',
      },
      {
        text: 'HT',
        align: 'center',
        sortable: true,
        value: 'ht',
      },
      {
        text: 'DE',
        align: 'center',
        sortable: true,
        value: 'de',
      },
      {
        text: 'EV',
        align: 'center',
        sortable: true,
        value: 'ev',
      },
    ],
  }),
  computed: {
    filteredDigimons(): Digimon[] {
      const filters: Filter = this.$store.getters.filter;
      const allDigimons: Digimon[] = this.$store.getters.digimons;

      return allDigimons.filter((digimon: Digimon) => {
        const shownByRank = filters.rank[digimon.rank];
        const shownByForm = filters.form[digimon.form];
        const shownByBasicAttribute = filters.basicAttribute[digimon.basicAttribute];
        const shownByAttribute = filters.attribute[digimon.attribute];

        return (
          shownByRank
          && shownByForm
          && shownByBasicAttribute
          && shownByAttribute
        );
      });
    },
  },
  methods: {
    toPercent(value: number): string {
      return `${(value * 100).toFixed(2)}%`;
    },
  },
});
