








































































import Vue from 'vue';
import { Filter } from '@/data/models/Filter';

export default Vue.extend({
  data: () => ({
    dialog: false,
    filter: new Filter() as Filter,
    filterCategories: [
      {
        text: 'Rank',
        categoryAttribute: 'rank',
        categoryItems: ['SSS+', 'SSS', 'SS+', 'SS', 'S+', 'S', 'A+', 'A'],
      },
      {
        text: 'Form',
        categoryAttribute: 'form',
        categoryItems: [
          'Rookie',
          'Champion',
          'Ultimate',
          'Mega',
          'Side Mega',
          'Burst Mode',
          'Jogress',
          'Variant',
          'Armor',
          'H-Hybrid',
          'B-Hybrid',
          'Z-Hybrid',
          'O-Hybrid',
          'Champion X',
          'Ultimate X',
          'Mega X',
          'Side Mega X',
          'Burst Mode X',
          'Jogress X',
        ],
      },
      {
        text: 'Basic Attribute',
        categoryAttribute: 'basicAttribute',
        categoryItems: ['None', 'Virus', 'Vaccine', 'Data', 'Unknown'],
      },
      {
        text: 'Elemental Attribute',
        categoryAttribute: 'attribute',
        categoryItems: [
          'Land',
          'Fire',
          'Ice',
          'Light',
          'Steel',
          'Neutral',
          'Pitch Black',
          'Thunder',
          'Water',
          'Wind',
          'Wood',
        ],
      },
    ],
  }),
  computed: {
    //
  },
  methods: {
    resetFilter(): void {
      this.$store.dispatch('setFilter', new Filter());
      this.dialog = false;
    },
    applyFilter(): void {
      this.$store.dispatch('setFilter', this.filter);
      this.dialog = false;
    },
  },
  created(): void {
    const filterFromStore = this.$store.getters.filter;

    this.filter = JSON.parse(JSON.stringify(filterFromStore));
  },
});
